.showAnswerActive {
  color: red;
}
.blank-show-anwser {
  position: relative;
  color: red;
}
.blank-show-anwser:before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background: #333;
}
