.answer-wrap {
  /*纠错*/
}
.answer-wrap .analyze-wrap-modal .analyze-error {
  cursor: pointer;
  color: #DE923F;
  font-size: 14px;
  margin-bottom: 0;
  text-align: right;
  position: absolute;
  right: 40px;
  top: 25px;
}
.answer-wrap .analyze-wrap-modal .analyze-error i {
  margin-right: 5px;
}
.answer-wrap .analyze-wrap-modal .analyze-modal-card .ant-card-head-title {
  white-space: normal;
  padding: 0 0 10px;
}
.answer-wrap .analyze-wrap-modal .ant-card-head {
  padding: 0 15px;
}
.answer-wrap .analyze-wrap-modal .ant-card-body {
  padding: 15px;
}
.answer-wrap .analyze-wrap-modal .ant-card-body .testing .testing-name {
  color: #27CDA2;
}
.answer-wrap .error-test-modal .ant-checkbox-wrapper + .ant-checkbox-wrapper {
  margin-left: 0;
}
.answer-wrap .error-test-modal .ant-checkbox-wrapper {
  margin-right: 8px;
}
.answer-wrap .question-select {
  margin-bottom: 20px;
}
.answer-wrap .analyze-collect {
  cursor: pointer;
  color: #DE923F;
  font-size: 14px;
  margin-bottom: 0;
  text-align: right;
  position: absolute;
  right: 110px;
  top: 25px;
}
